.underline {
  margin-top: 80px;
}

.underline_small-mt {
  margin-top: 40px;
}

.underline__list {
  align-items: center;
  display: flex;
  width: 100%;
  list-style-type: none;
  justify-content: space-between;
}

.underline__list_center {
  justify-content: center;
}

.underline__item {
  display: inline-block;
  width: 100%;
  max-width: 270px;
}

.underline__item + .underline__item {
  margin-left: 5px;
}

.underline__link {
  display: inline-block;
  width: 100%;
  padding: 20px;
  font-weight: 900;
  font-size: 21px;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  border-bottom: 7px solid transparent;
  transition: all 0.3s ease-out 0s;
}

.underline__link:hover, .underline__link.active {
  color: #ff0a36;
  border-color: #ff0a36;
}

@media screen and (max-width: 720px) {
  .underline__list {
    flex-wrap: wrap;
    justify-content: space-around;
  }
  .underline__item {
    flex-basis: 35%;
    margin-bottom: 15px;
  }
  .underline__link {
    border-bottom: 4px solid transparent;
  }
}

@media screen and (max-width: 420px) {
  .underline__item {
    flex-basis: 40%;
  }
  .underline__link {
    font-size: 18px;
    padding: 15px 10px;
  }
}
