.program {
  padding-bottom: 260px;
}

.program__title {
  width: 100%;
  text-align: center;
  display: block;
  font-weight: 700;
  font-size: 24px;
  text-transform: uppercase;
  margin-bottom: 50px;
}

.program__table {
  width: 100%;
}

.program__time + .program__name {
  margin-left: 50px;
}

.program__time {
  font-size: 18px;
  font-weight: 700;
  width: 50px;
  display: inline-block;
  color: inherit;
}

.program__name {
  font-size: 18px;
  font-weight: 500;
  color: inherit;
  text-decoration: underline;
  text-transform: uppercase;
}

.program__schedule-item {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 30px;
  border-left: 2px solid transparent;
  border-right: 2px solid transparent;
  border-top: 2px solid #252525;
  border-bottom: 2px solid #252525;
  color: #898989;
}

.program__schedule-item.disabled {
  color: #545454;
}

.program__schedule-item.disabled .program__name {
  text-decoration: none;
}

.program__schedule-item.active {
  color: #ff0a36;
  border-color: #ff0a36;
  border-image: linear-gradient(to right, #ff0a36, #ff6b3a) 1 40%;
}

@media screen and (max-width: 920px) {
  .program {
    padding-bottom: 100px;
  }
}

@media screen and (max-width: 520px) {
  .program {
    padding-bottom: 80px;
  }
  .program__time + .program__name {
    margin-left: 0px;
    margin-top: 15px;
  }
  .program__name {
    width: 100%;
    text-align: center;
    font-size: 16px;
  }
  .program__time {
    font-size: 18px;
  }
  .program__schedule-item {
    padding: 15px 10px;
    flex-direction: column;
  }
}
