@import "./varaibles";

.popup {
    position: fixed;
    z-index: 22;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .7);
    display: flex;
    align-items: center;
    justify-content: center;
    @include transition(all);
    opacity: 0;
    overflow-y: auto;
    visibility: hidden;
    pointer-events: none;
    padding: 10px;
    &__body, &__img {
        @include transition(all); }
    &__img {
        max-width: 90%;
        max-height: 90%;
        width: 100%;
        object-fit: cover;
        height: auto;
        text-align: center; }
    &__close {
        position: absolute;
        top: 10px;
        right: 10px;
        height: 40px;
        width: 40px;
        z-index: 3;
        &::before, &::after {
            content: '';
            display: inline-block;
            height: 2px;
            width: 26px;
            background-color: #fff;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%); }
        &::before {
            transform: translate(-50%, -50%) rotate(-45deg); }
        &::after {
            transform: translate(-50%, -50%) rotate(45deg); } }
    &.active &__body, &__img {
        opacity: 1; }
    &.active {
        opacity: 1;
        visibility: visible;
        pointer-events: all; } }
