@import "./varaibles";

.hallway {
    padding: 60px 0px 220px;
    &__list {
        display: flex;
        justify-content: space-between;
        align-items: center;
        list-style-type: none; }
    &__item {
        width: 100%;
        display: inline-block;
        & + & {
            margin-left: 5px; } }
    &__link {
        font-size: 21px;
        font-weight: 900;
        padding: 50px 20px;
        display: inline-block;
        width: 100%;
        text-align: center;
        border: 2px solid transparent;
        @include transition(all);
        &:hover, &.active {
            border-color: $text-color-accent;
            border-image: url('../img/hall-border.png') 2 round round; } } }

@media screen and ( max-width: 920px ) {
    .hallway {
        padding-bottom: 110px; } }

@media screen and ( max-width: 750px ) {
    .hallway {
        &__list {
            flex-wrap: wrap;
            justify-content: space-around; }
        &__item {
            flex-basis: 40%;
            & + & {
                margin-left: 0px;
                margin-top: 5px; } }
        &__link {
            padding: 30px 20px; } } }

@media screen and ( max-width: 520px ) {
    .hallway {
        padding-bottom: 80px; } }

@media screen and ( max-width: 450px ) {
    .hallway {
        &__item {
            flex-basis: 100%; }
        &__link {
            padding: 20px; } } }
