@import "./varaibles";

.reaction {
    $dimention: 30px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    position: relative;
    padding: {
        left: 70px;
        right: 70px; }
    &__checkbox {
        position: absolute;
        height: 1px;
        width: 1px;
        opacity: 0;
        visibility: hidden; }
    &__label {
        display: inline-block;
        height: $dimention; }
    &__label + &__list {
        margin-left: 40px; }
    &__checkbox:checked ~ &__list {
        visibility: visible;
        opacity: 1;
        pointer-events: all;
        transform: translateX(0px); }
    &__list {
        display: flex;
        align-items: center;
        justify-content: space-between;
        list-style-type: none;
        visibility: hidden;
        opacity: 0;
        pointer-events: none;
        transform: translateX(-20px);
        @include transition(all);
        background-color: #464646;
        box-shadow: 0 0 0 15px #464646;
        padding: 0px 10px;
        position: relative;
        &::before {
            content: '';
            display: inline-block;
            position: absolute;
            top: 50%;
            left: -15px;
            border: 7px solid transparent;
            border-right: 7px solid #464646;
            transform: translate(-100%, -50%); } }
    &__item {
        display: inline-block;
        height: $dimention;
        & + & {
            margin-left: 30px; }
        &:nth-child(1) .reaction__trigger {
            background-image: url(../img/like-2.png); }
        &:nth-child(2) .reaction__trigger {
            background-image: url(../img/like-3.png); }
        &:nth-child(3) .reaction__trigger {
            background-image: url(../img/like-4.png); }
        &:nth-child(4) .reaction__trigger {
            background-image: url(../img/like-5.png); }
        &:nth-child(5) .reaction__trigger {
            background-image: url(../img/like-6.png); } }
    &__trigger {
        display: inline-block;
        cursor: pointer;
        height: $dimention;
        width: $dimention;
        @include transition(transform);
        background-image: url(../img/like-1.png);
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        &:hover {
            transform: scale(1.05); } } }

@media screen and ( max-width: 520px ) {
    .reaction {
        $dimention: 25px;
        padding: {
            left: 0px;
            right: 0px; }
        &__label + &__list {
            margin-left: 30px; }
        &__label {
            height: $dimention; }
        &__list {
            box-shadow: 0 0 0 10px #464646;
            &::before {
                left: -10px; } }
        &__trigger {
            height: $dimention;
            width: $dimention; }
        &__item {
            height: $dimention;
            & + & {
                margin-left: 20px; } } } }
