.footer {
  flex-grow: 0;
  padding-bottom: 50px;
}

.footer__wrap {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  border-top: 2px solid #ffffff;
  padding-top: 35px;
  padding-right: 90px;
}

.footer__logos {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.footer__logos-list {
  display: flex;
  align-items: flex-end;
  list-style-type: none;
  margin-left: 50px;
}

.footer__logos-item + .footer__logos-item {
  margin-left: 30px;
}

.footer__logos-link {
  display: inline-block;
  height: 40px;
  width: 40px;
}

.footer__logos-link img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.footer__contacts {
  list-style-type: none;
}

.footer__item + .footer__item {
  margin-top: 3px;
}

.footer__link {
  font-weight: 700;
  font-size: 18px;
  white-space: nowrap;
}

.footer__bottom {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-top: 45px;
}

.footer__copyright {
  font-size: 12px;
  font-weight: 500;
  color: #898989;
  display: inline-block;
}

.footer__copyright:any-link:hover {
  text-decoration: underline;
}

.footer__copyright + .footer__copyright {
  margin-left: 50px;
}

@media screen and (max-width: 720px) {
  .footer__wrap, .footer__logos {
    flex-direction: column;
    align-items: flex-start;
  }
  .footer__logos-list {
    margin: 25px 0;
  }
  .footer__logos-link {
    height: 35px;
    width: 35px;
  }
  .footer__link {
    font-size: 16px;
  }
  .footer__bottom {
    flex-direction: column-reverse;
    align-items: flex-start;
    margin-top: 30px;
  }
  .footer__copyright + .footer__copyright {
    margin-left: 0px;
    margin-bottom: 20px;
  }
}
