@import "./varaibles";

.button {
    display: inline-block;
    border-radius: 0px;
    text-decoration: none;
    text-align: center;
    appearance: none;
    cursor: pointer;
    background-color: transparent;
    border: none;
    position: relative;
    outline: none;
    overflow: hidden;
    &_gradient-border {
        border: 3px solid $text-color-accent;
        border-image: linear-gradient(to right, $text-color-accent, $orange) 1 40%; } }
