@import "./varaibles";

.hero {
    padding-top: 170px;
    padding-bottom: 200px;
    &__wrap {
        display: flex;
        align-items: flex-start;
        justify-content: space-between; }
    &__body {
        max-width: 780px;
        width: 100%; }
    &__title {
        font-weight: bold;
        font-size: 36px;
        line-height: 50px;
        text-transform: uppercase;
        margin-bottom: 125px;
        position: relative;
        &::before {
            content: '';
            display: inline-block;
            width: 3px;
            position: absolute;
            background-color: $text-color;
            left: -30px;
            bottom: 12px;
            height: 525px;
            transform-origin: top;
            transform: scaleY(1);
            animation: grow-bottom 2s 1 linear 0s; } }
    &__deco {
        position: absolute;
        @include transition(transform);
        transition-duration: 0.05s;
        &:nth-of-type(1) {
            height: 1125px;
            width: 1125px;
            left: -880px;
            top: -14%; }
        &:nth-of-type(2) {
            height: 820px;
            width: 820px;
            right: -96px;
            top: -220px; } }
    &__date {
        display: flex;
        align-items: center;
        &-line {
            max-width: 290px;
            width: 100%;
            height: 2px;
            background-color: $text-color-accent;
            margin: 0px 10px; }
        &-text {
            white-space: nowrap;
            color: $text-color-accent;
            font-size: 36px;
            font-weight: bold; } }
    &__aside {
        max-width: 225px;
        padding-left: 30px;
        position: relative;
        &::before {
            content: '';
            display: inline-block;
            width: 4px;
            background-size: contain;
            background-image: url(../img/vertical-line-bg.png);
            background-repeat: repeat-y;
            position: absolute;
            background-color: transparent;
            left: 0px;
            top: 7px;
            height: 527px;
            transform-origin: top;
            transform: scaleY(1);
            animation: grow-bottom-height 2.6s 1 linear 0s; }
        &-text {
            font-size: 36px;
            line-height: 40px; } } }

@media screen and ( max-width: 1600px ) {
    .hero {
        &__deco {
            &:nth-of-type(1) {
                height: 820px;
                width: 820px;
                left: -620px;
                top: 4%; }
            &:nth-of-type(2) {
                height: 440px;
                width: 440px;
                right: -55px;
                top: -80px; } } } }

@media screen and ( max-width: 1366px ) {
    .hero {
        &__deco {
            opacity: .8;
            &:nth-of-type(1) {
                height: 640px;
                width: 640px;
                left: -500px;
                top: 12%; }
            &:nth-of-type(2) {
                height: 440px;
                width: 440px;
                right: -55px;
                top: -80px; } } } }

@media screen and ( max-width: 1310px ) {
    .hero {
        &__deco {
            &:nth-of-type(1) {
                display: none; } } } }

@media screen and ( max-width: 1070px ) {
    .hero {
        padding: 160px 0px 100px;
        &__deco {
            &:nth-of-type(2) {
                height: 400px;
                width: 400px;
                right: -55px;
                top: -80px; } }
        &__body {
            max-width: 730px; }
        &__title {
            font-size: 30px;
            margin-bottom: 80px; }
        &__aside {
            display: none; } } }

@media screen and ( max-width: 920px ) {
    .hero {
        padding-top: 110px;
        padding-bottom: 90px;
        &__deco {
            &:nth-of-type(2) {
                height: 350px;
                width: 350px;
                right: -90px;
                top: -90px; } } } }

@media screen and ( max-width: 720px ) {
    .hero {
        &__date {
            &-text {
                font-size: 30px; } } } }

@media screen and ( max-width: 520px ) {
    .hero {
        padding-top: 100px;
        padding-bottom: 50px;
        &__deco {
            &:nth-of-type(2) {
                height: 300px;
                width: 300px;
                right: -90px;
                top: -90px; } } } }

@media screen and ( max-width: 450px ) {
    .hero {
        &__title {
            font-size: 26px;
            margin-bottom: 60px; }
        &__date {
            &-text {
                font-size: 24px; } } } }

@media screen and ( max-width: 370px ) {
    .hero {
        padding-bottom: 60px;
        &__title {
            font-size: 22px;
            line-height: 36px;
            margin-bottom: 40px; } } }
