.button {
  display: inline-block;
  border-radius: 0px;
  text-decoration: none;
  text-align: center;
  appearance: none;
  cursor: pointer;
  background-color: transparent;
  border: none;
  position: relative;
  outline: none;
  overflow: hidden;
}

.button_gradient-border {
  border: 3px solid #ff0a36;
  border-image: linear-gradient(to right, #ff0a36, #ff6b3a) 1 40%;
}
