@import "./varaibles";

.program {
    padding-bottom: 260px;
    &__title {
        width: 100%;
        text-align: center;
        display: block;
        font-weight: 700;
        font-size: 24px;
        text-transform: uppercase;
        margin-bottom: 50px; }
    &__table {
        width: 100%; }
    &__time + &__name {
        margin-left: 50px; }
    &__time {
        font-size: 18px;
        font-weight: 700;
        width: 50px;
        display: inline-block;
        color: inherit; }
    &__name {
        font-size: 18px;
        font-weight: 500;
        color: inherit;
        text-decoration: underline;
        text-transform: uppercase; }
    &__schedule {
        &-item {
            display: flex;
            align-items: center;
            width: 100%;
            padding: 30px;
            border: {
                left: 2px solid transparent;
                right: 2px solid transparent;
                top: 2px solid #252525;
                bottom: 2px solid #252525; }
            color: $text-color-grey;
            &.disabled {
                color: #545454;
                .program {
                    &__name {
                        text-decoration: none; } } }
            &.active {
                color: $text-color-accent;
                border-color: $text-color-accent;
                border-image: linear-gradient(to right, $text-color-accent, $orange) 1 40%; } } } }

@media screen and ( max-width: 920px ) {
    .program {
        padding-bottom: 100px; } }

@media screen and ( max-width: 520px ) {
    .program {
        padding-bottom: 80px;
        &__time + &__name {
            margin-left: 0px;
            margin-top: 15px; }
        &__name {
            width: 100%;
            text-align: center;
            font-size: 16px; }
        &__time {
            font-size: 18px; }
        &__schedule {
            &-item {
                padding: 15px 10px;
                flex-direction: column; } } } }


