.popup {
  position: fixed;
  z-index: 22;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease-out 0s;
  opacity: 0;
  overflow-y: auto;
  visibility: hidden;
  pointer-events: none;
  padding: 10px;
}

.popup__body, .popup__img {
  transition: all 0.3s ease-out 0s;
}

.popup__img {
  max-width: 90%;
  max-height: 90%;
  width: 100%;
  object-fit: cover;
  height: auto;
  text-align: center;
}

.popup__close {
  position: absolute;
  top: 10px;
  right: 10px;
  height: 40px;
  width: 40px;
  z-index: 3;
}

.popup__close::before, .popup__close::after {
  content: '';
  display: inline-block;
  height: 2px;
  width: 26px;
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.popup__close::before {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.popup__close::after {
  transform: translate(-50%, -50%) rotate(45deg);
}

.popup.active .popup__body, .popup__img {
  opacity: 1;
}

.popup.active {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}
