@import "./varaibles";

.section-title {
    font-family: 'Neue Machina', sans-serif;
    font-size: 9.4vw;
    line-height: 11vw;
    font-weight: 700;
    width: 100%;
    white-space: nowrap;
    opacity: .07;
    text-transform: uppercase;
    margin-bottom: 10px;
    pointer-events: none;
    user-select: none; }

@media screen and ( max-width: 520px ) {
    .section-title {
        font-size: 18vw;
        line-height: 22vw; } }
