@import "./varaibles";

.underline {
    margin-top: 80px;
    &_small-mt {
        margin-top: 40px; }
    &__list {
        align-items: center;
        display: flex;
        width: 100%;
        list-style-type: none;
        justify-content: space-between;
        &_center {
            justify-content: center; } }
    &__item {
        display: inline-block;
        width: 100%;
        max-width: 270px;
        & + & {
            margin-left: 5px; } }
    &__link {
        display: inline-block;
        width: 100%;
        padding: 20px;
        font-weight: 900;
        font-size: 21px;
        text-transform: uppercase;
        text-align: center;
        color: $text-color;
        border-bottom: 7px solid transparent;
        @include transition(all);
        &:hover, &.active {
            color: $text-color-accent;
            border-color: $text-color-accent; } } }

@media screen and ( max-width: 720px ) {
    .underline {
        &__list {
            flex-wrap: wrap;
            justify-content: space-around; }
        &__item {
            flex-basis: 35%;
            margin-bottom: 15px; }
        &__link {
            border-bottom: 4px solid transparent; } } }

@media screen and ( max-width: 420px ) {
    .underline {
        &__list {}
        &__item {
            flex-basis: 40%; }
        &__link {
            font-size: 18px;
            padding: 15px 10px; } } }
