.header {
  position: fixed;
  z-index: 5;
  width: 100%;
  left: 0;
  top: 0;
  padding: 30px 0px;
  transition: all 0.3s ease-out 0s;
  flex-grow: 0;
}

.header.fixed {
  padding: 15px 0px;
  background-color: rgba(0, 0, 0, 0.85);
}

.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

@media screen and (max-width: 920px) {
  .header .burger {
    display: inline-block !important;
  }
  .header .nav {
    display: none;
  }
}

@media screen and (max-width: 720px) {
  .header {
    padding: 15px 0px;
  }
  .header.fixed {
    padding: 10px 0px;
  }
}
