@import "./varaibles";

.translation {
    padding: 100px 0px 180px;
    &__title {
        font-size: 24px;
        text-transform: uppercase;
        text-align: center;
        font-weight: 700;
        color: $text-color;
        margin-bottom: 15px;
        width: 100%; }
    &__reaction {
        padding: {
            top: 20px;
            bottom: 20px; } }
    iframe {
        width: 100%;
        height: 660px; } }

@media screen and ( max-width: 1070px ) {
    .translation {
        iframe {
            height: 460px; } } }

@media screen and ( max-width: 920px ) {
    .translation {
        padding-top: 90px;
        padding-bottom: 110px; } }

@media screen and ( max-width: 520px ) {
    .translation {
        padding-bottom: 80px;
        iframe {
            height: 400px; } } }

@media screen and ( max-width: 420px ) {
    .translation {
        &__title {
            font-size: 20px; }
        iframe {
            height: 280px; } } }
