@import "./varaibles";

.gallery {
    padding-bottom: 220px;
    &__swiper {
        overflow: hidden;
        &-wrap {
            position: relative; } }
    &__slide {
        display: flex;
        align-items: flex-start;
        justify-content: center; }
    &__col {
        width: 100%;
        &:nth-child(1) {
            max-width: 270px; }
        &:nth-child(2) {
            max-width: 470px; }
        &:nth-child(3) {
            max-width: 360px;
            .gallery {
                &__item {
                    &_small {
                        padding-bottom: 75%; } } } } }
    &__item {
        display: block;
        position: relative;
        flex-shrink: 0;
        width: 100%;
        padding-bottom: 100%;
        &_large {
            max-width: 470px; }
        &_medium {
            max-width: 360px; }
        &_small {
            max-width: 270px; }
        & + & {
            margin-top: 30px; } }
    &__img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover; }
    &__col {
        & + & {
            margin-left: 30px; } }
    &__control {
        vertical-align: middle;
        text-align: center;
        font-weight: 700;
        font-size: 60px;
        line-height: 44px;
        opacity: 0.2;
        @include transition(all);
        top: 180px;
        position: absolute;
        z-index: 3;
        &:hover {
            color: $text-color-accent;
            opacity: 1; }
        &_prev {
            left: -40px;
            transform: translate(0, -50%); }
        &_next {
            right: -40px;
            transform: translate(0, -50%); } } }

@media screen and ( max-width: 1070px ) {
    .gallery {
        &__col {
            & + & {
                margin-left: 15px; } }
        &__item {
            & + & {
                margin-top: 15px; } } } }

@media screen and ( max-width: 970px ) {
    .gallery {
        &__col:nth-child(3) .gallery__item_small {
            padding-bottom: 100%; } } }

@media screen and ( max-width: 920px ) {
    .gallery {
        padding-bottom: 110px; } }

@media screen and ( max-width: 520px ) {
    .gallery {
        padding-bottom: 80px;
        &__slide {
            flex-wrap: wrap; }
        &__col {
            &:nth-child(1), &:nth-child(2) {
                flex-basis: 48%; }
            &:nth-child(2) {
                margin-left: 10px; }
            &:nth-child(3) {
                margin-left: 0;
                margin-top: 10px;
                flex-basis: 100%; } }
        &__item {
            max-width: none;
            & + & {
                margin-top: 10px; } } } }
