@import "./varaibles";

.tab {
    &__triggers {
        padding-bottom: 20px;
        display: flex;
        align-items: center;
        list-style-type: none;
        justify-content: space-between;
        border-bottom: 2px solid #fff;
        margin-bottom: 50px; }
    &__item {
        flex-grow: 1;
        width: 100%;
        & + & {
            margin-left: 5px; } }
    &__trigger {
        width: 100%;
        padding: 20px;
        font-size: 30px;
        height: 78px;
        @include transition(all);
        transition-duration: .1s;
        border: 3px solid transparent;
        span {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            font-size: inherit;
            font-weight: inherit;
            color: inherit; }
        &:hover, &.active {
            font-weight: 700;
            color: $text-color-accent;
            border: 3px solid $text-color-accent;
            border-image: linear-gradient(to right, $text-color-accent, $orange) 1 40%; } }
    &__body {
        display: none;
        &.active {
            display: block; } } }

@media screen and ( max-width: 720px ) {
    .tab {
        &__triggers {
            flex-direction: column;
            align-items: center; }
        &__item {
            max-width: 300px;
            & + & {
                margin-left: 0px;
                margin-top: 5px; } } } }

@media screen and ( max-width: 450px ) {
    .tab {
        &__trigger {
            height: 68px;
            padding: 15px;
            font-size: 24px; } } }
