.reaction {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  padding-left: 70px;
  padding-right: 70px;
}

.reaction__checkbox {
  position: absolute;
  height: 1px;
  width: 1px;
  opacity: 0;
  visibility: hidden;
}

.reaction__label {
  display: inline-block;
  height: 30px;
}

.reaction__label + .reaction__list {
  margin-left: 40px;
}

.reaction__checkbox:checked ~ .reaction__list {
  visibility: visible;
  opacity: 1;
  pointer-events: all;
  transform: translateX(0px);
}

.reaction__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  list-style-type: none;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transform: translateX(-20px);
  transition: all 0.3s ease-out 0s;
  background-color: #464646;
  box-shadow: 0 0 0 15px #464646;
  padding: 0px 10px;
  position: relative;
}

.reaction__list::before {
  content: '';
  display: inline-block;
  position: absolute;
  top: 50%;
  left: -15px;
  border: 7px solid transparent;
  border-right: 7px solid #464646;
  transform: translate(-100%, -50%);
}

.reaction__item {
  display: inline-block;
  height: 30px;
}

.reaction__item + .reaction__item {
  margin-left: 30px;
}

.reaction__item:nth-child(1) .reaction__trigger {
  background-image: url(../img/like-2.png);
}

.reaction__item:nth-child(2) .reaction__trigger {
  background-image: url(../img/like-3.png);
}

.reaction__item:nth-child(3) .reaction__trigger {
  background-image: url(../img/like-4.png);
}

.reaction__item:nth-child(4) .reaction__trigger {
  background-image: url(../img/like-5.png);
}

.reaction__item:nth-child(5) .reaction__trigger {
  background-image: url(../img/like-6.png);
}

.reaction__trigger {
  display: inline-block;
  cursor: pointer;
  height: 30px;
  width: 30px;
  transition: transform 0.3s ease-out 0s;
  background-image: url(../img/like-1.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.reaction__trigger:hover {
  transform: scale(1.05);
}

@media screen and (max-width: 520px) {
  .reaction {
    padding-left: 0px;
    padding-right: 0px;
  }
  .reaction__label + .reaction__list {
    margin-left: 30px;
  }
  .reaction__label {
    height: 25px;
  }
  .reaction__list {
    box-shadow: 0 0 0 10px #464646;
  }
  .reaction__list::before {
    left: -10px;
  }
  .reaction__trigger {
    height: 25px;
    width: 25px;
  }
  .reaction__item {
    height: 25px;
  }
  .reaction__item + .reaction__item {
    margin-left: 20px;
  }
}
