@import "./varaibles";

.footer {
    flex-grow: 0;
    padding-bottom: 50px;
    &__wrap {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        border-top: 2px solid #ffffff;
        padding-top: 35px;
        padding-right: 90px; }
    &__logos {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        &-list {
            display: flex;
            align-items: flex-end;
            list-style-type: none;
            margin-left: 50px; }
        &-item {
            & + & {
                margin-left: 30px; } }
        &-link {
            display: inline-block;
            height: 40px;
            width: 40px;
            img {
                height: 100%;
                width: 100%;
                object-fit: cover; } } }
    &__contacts {
        list-style-type: none; }
    &__item {
        & + & {
            margin-top: 3px; } }
    &__link {
        font-weight: 700;
        font-size: 18px;
        white-space: nowrap; }
    &__bottom {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        margin-top: 45px; }
    &__copyright {
        font-size: 12px;
        font-weight: 500;
        color: $text-color-grey;
        display: inline-block;
        &:any-link {
            &:hover {
                text-decoration: underline; } }
        & + & {
            margin-left: 50px; } } }

@media screen and ( max-width: 720px ) {
    .footer {
        &__wrap, &__logos {
            flex-direction: column;
            align-items: flex-start; }
        &__logos {
            &-list {
                margin: 25px 0; }
            &-link {
                height: 35px;
                width: 35px; } }
        &__link {
            font-size: 16px; }
        &__bottom {
            flex-direction: column-reverse;
            align-items: flex-start;
            margin-top: 30px; }
        &__copyright {
            & + & {
                margin-left: 0px;
                margin-bottom: 20px; } } } }

