@import "./varaibles";

.form {
    &__row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        & + &_center {
            margin-top: 30px; }
        &_center {
            justify-content: center;
            & > * + * {
                margin-left: 30px; } }
        .button:not(.form__pass-toggle) {
            height: 78px;
            line-height: 68px;
            width: 100%;
            max-width: 470px;
            font-weight: 700;
            font-size: 22px; } }
    &__col {
        flex-grow: 1;
        & + & {
            margin-left: 30px; } }
    &__submit {
        color: #ffffff;
        text-transform: uppercase;
        background-image: url(../img/submit-btn-bg.jpg);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center; }
    &__support {
        font-size: 30px !important;
        img {
            margin-left: 20px;
            vertical-align: middle; } }
    &__input {
        background-color: transparent;
        border: none;
        border-bottom: 3px solid $text-color-grey;
        display: block;
        width: 100%;
        padding: 30px;
        font-size: 24px;
        font-weight: bold;
        outline: none;
        resize: none; }
    &__pass-toggle {
        display: inline-block;
        height: 40px;
        width: 40px;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        background-image: url(../img/eye-icon.png);
        position: absolute;
        right: 0;
        top: 50%;
        transform: translate(-50%, -50%);
        @include transition(opacity);
        opacity: .4;
        &:hover, &.active {
            opacity: 1; } } }

@media screen and ( max-width: 750px ) {
    .form {
        &__row {
            flex-direction: column;
            &_center {
                & > * + * {
                    margin-left: 0;
                    margin-top: 20px; }
                .button {
                    height: 70px;
                    line-height: 60px;
                    font-size: 20px; } }
            &:not(&_center) {
                align-items: stretch; } }
        &__col {
            & + & {
                margin-left: 0px; } }
        &__support {
            font-size: 24px !important;
            img {
                height: 30px;
                width: 30px; } }
        &__input {
            padding: 20px;
            font-size: 18px; } } }

@media screen and ( max-width: 450px ) {
    .form {
        &__row {
            &_center {
                .button {
                    height: 60px;
                    line-height: 50px;
                    font-size: 18px; } } }
        &__support {
            font-size: 20px !important; }
        &__input {
            padding: 20px 10px;
            font-size: 16px; } } }
