@import "./varaibles";

.program-table {
    &__row {
        display: flex;
        align-items: flex-start;
        justify-content: center; }
    &__column {
        width: 100%;
        padding-right: 20px; }
    &__main-column {
        flex-basis: 25%;
        & + & {
            .program-table__column {
                border-left: 2px solid $text-color-grey;
                padding-left: 15px; } } }
    &__cell_head {
        font-size: 21px;
        font-weight: 400;
        padding: 0px 30px 30px;
        text-align: center;
        border-bottom: 2px solid $text-color-grey;
        color: $text-color;
        margin-bottom: 30px; }
    &__column &__cell:not(&__cell_head) {
        display: flex; }
    &__column &__cell + &__cell {
        margin-top: 50px; }
    &__cell {
        width: 100%;
        color: $text-color-grey;
        &.accent {
            color: $text-color-accent; } }
    &__time + &__name {
        margin-left: 30px; }
    &__time {
        flex-shrink: 0;
        display: inline-block;
        font-weight: 700;
        font-size: 18px;
        width: 53px;
        color: inherit; }
    &__name {
        color: inherit;
        font-weight: 500;
        font-size: 16px;
        text-transform: uppercase;
        display: inline-block; } }

@media screen and ( max-width: 1140px ) {
    .program-table {
        &__row {
            display: grid;
            justify-content: initial;
            row-gap: 60px;
            grid-template-columns: repeat(auto-fit, minmax(380px, 1fr)); }
        &__main-column {
            & + & {
                .program-table__column {
                    border-left: none; } }
            &:nth-child(2n) {
                .program-table__column {
                    padding-left: 30px;
                    border-left: 2px solid $text-color-grey; } } } } }

@media screen and ( max-width: 830px ) {
    .program-table {
        &__row {
            grid-template-columns: repeat(auto-fit, minmax(280px, 1fr)); } } }

@media screen and ( max-width: 588px ) {
    .program-table {
        &__time + &__name {
            margin-left: 15px; }
        &__main-column {
            &:nth-child(2n) {
                .program-table__column {
                    padding: 0px 15px;
                    border-left: none; } } } } }
