@import "./varaibles";

.nav {
    width: 100%;
    max-width: 810px;
    &_header {
        margin-left: 50px; }
    &__list {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        list-style-type: none; }
    &__item {
        display: inline-block; }
    &__link {
        font-weight: bold;
        font-size: 18px;
        display: inline-block;
        position: relative;
        padding: 5px 0px;
        &::after {
            content: '';
            display: inline-block;
            position: absolute;
            width: 100%;
            left: 0;
            bottom: 0;
            height: 2px;
            background-color: currentColor;
            transform: scaleX(0);
            transform-origin: left;
            @include transition(transform); }
        &:hover {
            &::after {
                transform: scaleX(0.5); } } } }
