.hallway {
  padding: 60px 0px 220px;
}

.hallway__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
}

.hallway__item {
  width: 100%;
  display: inline-block;
}

.hallway__item + .hallway__item {
  margin-left: 5px;
}

.hallway__link {
  font-size: 21px;
  font-weight: 900;
  padding: 50px 20px;
  display: inline-block;
  width: 100%;
  text-align: center;
  border: 2px solid transparent;
  transition: all 0.3s ease-out 0s;
}

.hallway__link:hover, .hallway__link.active {
  border-color: #ff0a36;
  border-image: url("../img/hall-border.png") 2 round round;
}

@media screen and (max-width: 920px) {
  .hallway {
    padding-bottom: 110px;
  }
}

@media screen and (max-width: 750px) {
  .hallway__list {
    flex-wrap: wrap;
    justify-content: space-around;
  }
  .hallway__item {
    flex-basis: 40%;
  }
  .hallway__item + .hallway__item {
    margin-left: 0px;
    margin-top: 5px;
  }
  .hallway__link {
    padding: 30px 20px;
  }
}

@media screen and (max-width: 520px) {
  .hallway {
    padding-bottom: 80px;
  }
}

@media screen and (max-width: 450px) {
  .hallway__item {
    flex-basis: 100%;
  }
  .hallway__link {
    padding: 20px;
  }
}
