.form__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

.form__row + .form__row_center {
  margin-top: 30px;
}

.form__row_center {
  justify-content: center;
}

.form__row_center > * + * {
  margin-left: 30px;
}

.form__row .button:not(.form__pass-toggle) {
  height: 78px;
  line-height: 68px;
  width: 100%;
  max-width: 470px;
  font-weight: 700;
  font-size: 22px;
}

.form__col {
  flex-grow: 1;
}

.form__col + .form__col {
  margin-left: 30px;
}

.form__submit {
  color: #ffffff;
  text-transform: uppercase;
  background-image: url(../img/submit-btn-bg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.form__support {
  font-size: 30px !important;
}

.form__support img {
  margin-left: 20px;
  vertical-align: middle;
}

.form__input {
  background-color: transparent;
  border: none;
  border-bottom: 3px solid #898989;
  display: block;
  width: 100%;
  padding: 30px;
  font-size: 24px;
  font-weight: bold;
  outline: none;
  resize: none;
}

.form__pass-toggle {
  display: inline-block;
  height: 40px;
  width: 40px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(../img/eye-icon.png);
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: opacity 0.3s ease-out 0s;
  opacity: .4;
}

.form__pass-toggle:hover, .form__pass-toggle.active {
  opacity: 1;
}

@media screen and (max-width: 750px) {
  .form__row {
    flex-direction: column;
  }
  .form__row_center > * + * {
    margin-left: 0;
    margin-top: 20px;
  }
  .form__row_center .button {
    height: 70px;
    line-height: 60px;
    font-size: 20px;
  }
  .form__row:not(.form__row_center) {
    align-items: stretch;
  }
  .form__col + .form__col {
    margin-left: 0px;
  }
  .form__support {
    font-size: 24px !important;
  }
  .form__support img {
    height: 30px;
    width: 30px;
  }
  .form__input {
    padding: 20px;
    font-size: 18px;
  }
}

@media screen and (max-width: 450px) {
  .form__row_center .button {
    height: 60px;
    line-height: 50px;
    font-size: 18px;
  }
  .form__support {
    font-size: 20px !important;
  }
  .form__input {
    padding: 20px 10px;
    font-size: 16px;
  }
}
