.translation {
  padding: 100px 0px 180px;
}

.translation__title {
  font-size: 24px;
  text-transform: uppercase;
  text-align: center;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 15px;
  width: 100%;
}

.translation__reaction {
  padding-top: 20px;
  padding-bottom: 20px;
}

.translation iframe {
  width: 100%;
  height: 660px;
}

@media screen and (max-width: 1070px) {
  .translation iframe {
    height: 460px;
  }
}

@media screen and (max-width: 920px) {
  .translation {
    padding-top: 90px;
    padding-bottom: 110px;
  }
}

@media screen and (max-width: 520px) {
  .translation {
    padding-bottom: 80px;
  }
  .translation iframe {
    height: 400px;
  }
}

@media screen and (max-width: 420px) {
  .translation__title {
    font-size: 20px;
  }
  .translation iframe {
    height: 280px;
  }
}
