.tab__triggers {
  padding-bottom: 20px;
  display: flex;
  align-items: center;
  list-style-type: none;
  justify-content: space-between;
  border-bottom: 2px solid #fff;
  margin-bottom: 50px;
}

.tab__item {
  flex-grow: 1;
  width: 100%;
}

.tab__item + .tab__item {
  margin-left: 5px;
}

.tab__trigger {
  width: 100%;
  padding: 20px;
  font-size: 30px;
  height: 78px;
  transition: all 0.3s ease-out 0s;
  transition-duration: .1s;
  border: 3px solid transparent;
}

.tab__trigger span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: inherit;
  font-weight: inherit;
  color: inherit;
}

.tab__trigger:hover, .tab__trigger.active {
  font-weight: 700;
  color: #ff0a36;
  border: 3px solid #ff0a36;
  border-image: linear-gradient(to right, #ff0a36, #ff6b3a) 1 40%;
}

.tab__body {
  display: none;
}

.tab__body.active {
  display: block;
}

@media screen and (max-width: 720px) {
  .tab__triggers {
    flex-direction: column;
    align-items: center;
  }
  .tab__item {
    max-width: 300px;
  }
  .tab__item + .tab__item {
    margin-left: 0px;
    margin-top: 5px;
  }
}

@media screen and (max-width: 450px) {
  .tab__trigger {
    height: 68px;
    padding: 15px;
    font-size: 24px;
  }
}
