.program-table__row {
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.program-table__column {
  width: 100%;
  padding-right: 20px;
}

.program-table__main-column {
  flex-basis: 25%;
}

.program-table__main-column + .program-table__main-column .program-table__column {
  border-left: 2px solid #898989;
  padding-left: 15px;
}

.program-table__cell_head {
  font-size: 21px;
  font-weight: 400;
  padding: 0px 30px 30px;
  text-align: center;
  border-bottom: 2px solid #898989;
  color: #ffffff;
  margin-bottom: 30px;
}

.program-table__column .program-table__cell:not(.program-table__cell_head) {
  display: flex;
}

.program-table__column .program-table__cell + .program-table__cell {
  margin-top: 50px;
}

.program-table__cell {
  width: 100%;
  color: #898989;
}

.program-table__cell.accent {
  color: #ff0a36;
}

.program-table__time + .program-table__name {
  margin-left: 30px;
}

.program-table__time {
  flex-shrink: 0;
  display: inline-block;
  font-weight: 700;
  font-size: 18px;
  width: 53px;
  color: inherit;
}

.program-table__name {
  color: inherit;
  font-weight: 500;
  font-size: 16px;
  text-transform: uppercase;
  display: inline-block;
}

@media screen and (max-width: 1140px) {
  .program-table__row {
    display: grid;
    justify-content: initial;
    row-gap: 60px;
    grid-template-columns: repeat(auto-fit, minmax(380px, 1fr));
  }
  .program-table__main-column + .program-table__main-column .program-table__column {
    border-left: none;
  }
  .program-table__main-column:nth-child(2n) .program-table__column {
    padding-left: 30px;
    border-left: 2px solid #898989;
  }
}

@media screen and (max-width: 830px) {
  .program-table__row {
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  }
}

@media screen and (max-width: 588px) {
  .program-table__time + .program-table__name {
    margin-left: 15px;
  }
  .program-table__main-column:nth-child(2n) .program-table__column {
    padding: 0px 15px;
    border-left: none;
  }
}
