.gallery {
  padding-bottom: 220px;
}

.gallery__swiper {
  overflow: hidden;
}

.gallery__swiper-wrap {
  position: relative;
}

.gallery__slide {
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.gallery__col {
  width: 100%;
}

.gallery__col:nth-child(1) {
  max-width: 270px;
}

.gallery__col:nth-child(2) {
  max-width: 470px;
}

.gallery__col:nth-child(3) {
  max-width: 360px;
}

.gallery__col:nth-child(3) .gallery__item_small {
  padding-bottom: 75%;
}

.gallery__item {
  display: block;
  position: relative;
  flex-shrink: 0;
  width: 100%;
  padding-bottom: 100%;
}

.gallery__item_large {
  max-width: 470px;
}

.gallery__item_medium {
  max-width: 360px;
}

.gallery__item_small {
  max-width: 270px;
}

.gallery__item + .gallery__item {
  margin-top: 30px;
}

.gallery__img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.gallery__col + .gallery__col {
  margin-left: 30px;
}

.gallery__control {
  vertical-align: middle;
  text-align: center;
  font-weight: 700;
  font-size: 60px;
  line-height: 44px;
  opacity: 0.2;
  transition: all 0.3s ease-out 0s;
  top: 180px;
  position: absolute;
  z-index: 3;
}

.gallery__control:hover {
  color: #ff0a36;
  opacity: 1;
}

.gallery__control_prev {
  left: -40px;
  transform: translate(0, -50%);
}

.gallery__control_next {
  right: -40px;
  transform: translate(0, -50%);
}

@media screen and (max-width: 1070px) {
  .gallery__col + .gallery__col {
    margin-left: 15px;
  }
  .gallery__item + .gallery__item {
    margin-top: 15px;
  }
}

@media screen and (max-width: 970px) {
  .gallery__col:nth-child(3) .gallery__item_small {
    padding-bottom: 100%;
  }
}

@media screen and (max-width: 920px) {
  .gallery {
    padding-bottom: 110px;
  }
}

@media screen and (max-width: 520px) {
  .gallery {
    padding-bottom: 80px;
  }
  .gallery__slide {
    flex-wrap: wrap;
  }
  .gallery__col:nth-child(1), .gallery__col:nth-child(2) {
    flex-basis: 48%;
  }
  .gallery__col:nth-child(2) {
    margin-left: 10px;
  }
  .gallery__col:nth-child(3) {
    margin-left: 0;
    margin-top: 10px;
    flex-basis: 100%;
  }
  .gallery__item {
    max-width: none;
  }
  .gallery__item + .gallery__item {
    margin-top: 10px;
  }
}
